 <template>
  <div class="top">
    <div>
      <van-tabs
        v-model="active"
        @click="tabs"
        :class="{ tabs: pointerEvents, tabsNew: !pointerEvents }"
      >
        <van-tab
          v-for="(item, index) of tabsList"
          :key="index"
          :title="item.title"
        >
          <div :style="{ height: clientHeight + 'px' }" class="pdfNew">
            <pdf :pdfSrc="item.pdfSrc" class="pdf-pointer"></pdf></div
        ></van-tab>
      </van-tabs>
    </div>
    <div class="button">
      <van-button
        class="but"
        type="info"
        @click="previousPage"
        :disabled="previousPageDisabled"
        ></van-button
      >
      <van-button class="but2" type="primary" @click="but"
        >{{ page + "/" + total }}</van-button
      >
    </div>
  </div>
</template>
 
 <script>
import pdf from "./components/pdf.vue";

export default {
  name: "",
  components: { pdf },

  data() {
    return {
      active: 0,
      page: 1,
      total: 0,
      clientHeight: document.documentElement.clientHeight - 150,
      //   tabs
      pointerEvents: true,
      previousPageDisabled: true,
      
      tabsList: [
        {
          title: "",
          pdfSrc:
            "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf",
        },
        {
          title: "",
          pdfSrc:
            "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf",
        },
        {
          title: "",
          pdfSrc: "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf",
        },
        {
          title: "",
          pdfSrc:
            "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf",
        },
      ],
    };
  },

  created() {},
  mounted() {
    this.total = this.tabsList.length;
  },
  methods: {
    // 
    previousPage() {
      this.page--;
      console.log("", this.page);
      if (this.page <= 1) {
        this.previousPageDisabled = true;
        this.page = 1;
      } else {
        this.previousPageDisabled = false;
      }
      this.active = this.page - 1;
    },
    // 
    but() {
      console.log("asdasd", this.page, this.total);
      if (this.page == this.total - 1) {
        this.pointerEvents = false;
      }
      if (this.page == this.total) {
        this.page = this.total;

        // 
        this.checkstand();
      } else {
        this.page++;
      }
      this.active = this.page - 1;
    },

    // 

    checkstand() {
      const toast = this.$toast.loading({
        duration: 0, //  toast
        forbidClick: true,
        message: "",
      });

      let second = 3;
      const timer = setInterval(() => {
        second--;
        if (second) {
          toast.message = ` ${second} `;
        } else {
          clearInterval(timer);
          //  Toast
          this.$toast.clear();
        }
        if (second == 0) {
          this.$toast.success("");
          this.$router.push({
            path:'/cardResults'
          })
          
        }
      }, 1000);
    },
    // tabs
    tabs(name, title) {
      console.log(name, title);
    },
  },
  watch: {
    page(a, b) {
      console.log(a, b);
      if (a > 1) {
        this.previousPageDisabled = false;
      }
    },
  },
  computed: {},
};
</script>
 
 <style lang='scss' scoped>
.top {
  padding: 10px;
}
.pdfNew {
  margin-top: 20px;
  width: 100%;
  overflow: auto;
  border: #1989fa solid 1px;
}

:deep(.van-tab--active) {
  color: #1989fa;
  font-weight: 600;
}

:deep(.van-tabs__line) {
  position: absolute;
  bottom: 15px;
  left: 0;
  z-index: 1;
  width: 40px;
  height: 3px;
  background-color: #1989fa;
  border-radius: 3px;
}
.but {
  width: 40%;
  position: fixed;
  left: 10px;
  bottom: 10px;
  border-radius: 18px;
}
.but2 {
  width: 40%;
  position: fixed;
  right: 10px;
  bottom: 10px;
  border-radius: 18px;
}

// .tabs {
//   pointer-events: none;
// }
// .tabsNew {
//   pointer-events: auto;
// }
// 
:deep(.van-tab) {
 
  pointer-events: none;
}
.pdf-pointer {
  pointer-events: auto !important;
}
.button {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}
</style>
 