<template>
  <div class="pdf">
    <pdf v-for="i in numPages" :key="i" :src="laoclUrl" :page="i"></pdf>
  </div>
</template>
  
  <script>
import pdf from "vue-pdf";
export default {
  name: "index",
  props: {
    // pdf
    // pdfUrl: {
    //   type: String,
    //   default:
    //     "http://storage.xuetangx.com/public_assets/xuetangx/PDF/PlayerAPI_v1.0.6.pdf",
    // },

    pdfSrc: {
      type: String,
    },
  },
  components: {
    pdf,
  },
  data() {
    return {
      // 
      numPages: 1,
      // 
      laoclUrl: "",
    };
  },
  watch: {},
  computed: {},
  created() {
    this.laoclUrl = this.pdfSrc;
  },
  mounted() {
    this.getNumPages();
  },
  methods: {
    getNumPages() {
      let loadingTask = pdf.createLoadingTask(this.laoclUrl);
      //  promise emmmm... 
      loadingTask.promise
        .then((pdf) => {
          this.laoclUrl = loadingTask;
          this.numPages = pdf.numPages;
        })
        .catch((err) => {
          console.error("pdf");
        });
    },
  },
};
</script>
  
  <style lang="scss" scoped>
</style>
  